/* base */
@import "./base/vars.scss";
@import "./base/typography.scss";
@import "./base/grid.scss";
@import "./base/mixin.scss";
@import "./base/ui.scss";

// fonts
@import "fonts.scss";

/* layout */
@import "./layout/inc-header.scss";
@import "./layout/inc-footer.scss";

/* pages */
@import "splide";
@import "lightgallery";
@import "index";
@import "exhibition";
@import "news";
@import "pages";
@import "gallery";
@import "page-404";
@import "news-page";
@import "vacancy";
@import "development";

* {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  min-height: 100%;
  height: 100%;
  font-size: 16px;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100%;
  font-family: $baseFont;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.42;
  color: #fff;
  background-color: #121212;
}

#wrapper {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  min-height: 100%;
  min-width: 100%;
}

#middle {
  flex: 1 0 auto;
  -webkit-box-flex: 1;
}

#footer {
  flex: 0 0 auto;
  -webkit-box-flex: 0;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: $widthContainer;
  padding-left: $paddingContainer;
  padding-right: $paddingContainer;
}